import Button from '@mui/material/Button';
import logo from '../../assets/logo.png'
import { Box, spacing } from '@mui/system';
import Stack from '@mui/material/Stack';
import img1 from '../../assets/imgs/303.png'
import img2 from '../../assets/imgs/304.png'
import img3 from '../../assets/imgs/305.png'
import car from '../../assets/imgs/carro.png'
import backgroundSemiFooter from '../../assets/imgs/backgroundHome.jpg'
import img1SemiFooter from '../../assets/imgs/autoserv1.jpg'
import img2SemiFooter from '../../assets/imgs/autoserv2.jpg'
import img3SemiFooter from '../../assets/imgs/autoserv3.jpg'
import img4SemiFooter from '../../assets/imgs/autoserv4.jpg'
import img1SideFooter from '../../assets/imgs/footerSide.png'
import img2PosSideFooter from '../../assets/imgs/306.png'

import { Header, Menu, Card, CardHeader, CardBody, Box as Bx } from 'grommet';



const Home = () => {
    const middleButton = { textAlign: 'center', color: 'white', fontSize: 20 }
    const footerButton1 = { textAlign: 'center', color: 'white', fontSize: 40 }
    const footerButton2 = { textAlign: 'center', color: 'white', fontSize: 20 }
    const style = {
        navbarAtual: { backgroundColor: 'rgb(243 194 102)', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh' },
        navbarSecundario: { backgroundColor: 'transparent', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh' }
        // cardsServices: { }
    }
    return (
        <>
            <Header pad={{ left: 'xlarge', right: 'xlarge' }} background={{ color: 'rgb(179 179 179)' }}>
                <img src={logo} style={{ height: '10vh' }} />

                <Stack direction="row" mt={2} spacing={2} sx={{ position: 'relative' }}>
                    <Button variant="contained" sx={style.navbarAtual}>Home</Button>
                    <Button variant="contained" sx={style.navbarSecundario}>Sobre nós</Button>
                    <Button variant="contained" sx={style.navbarSecundario}>Serviços</Button>
                    <Button variant="contained" sx={style.navbarSecundario}>Contato</Button>
                    <Button variant="contained" sx={style.navbarSecundario}>Fazer Orçamento</Button>
                </Stack>
            </Header>

            <Box>
                <Stack direction="column" style={{
                    justifyContent: 'center',
                    alignItems: 'center',
                    backgroundColor: 'rgb(0 0 0 / 50%)',
                    borderRadius: 4,
                    width: '20%',
                    height: '20vh',
                    display: 'flex',
                    marginTop: '30%',
                    marginBottom: '6vh',
                    marginLeft: '5%'
                }}>
                    <h1 style={{ color: 'white', fontSize: 15, textAlign: 'center' }}>Martelinho, Funilaria e Pintura perto de você </h1>
                    <Button variant="contained" sx={{ backgroundColor: 'rgb(243 194 102)', width: '70%' }} color="error">Fazer Orçamento</Button>
                </Stack>

                <video style={{
                    position: 'absolute',
                    width: '100%',
                    left: '50%',
                    top: '50%',
                    height: '100%',
                    objectFit: 'cover',
                    transform: "translate(-50%, -50%)",
                    zIndex: "-1"
                }} loop autoPlay muted >

                    <source src={"https://precisionautocollision.com/wp-content/uploads/2020/01/Precision-Video-1.mp4"} type="video/mp4" />

                    Your browser does not support the video tag.
                </video>
            </Box>

            <Bx direction='row' background='rgb(179 179 179)' pad={'small'} style={{ justifyContent: 'center' }} gap='large'>
                <Card height="medium" width="medium" background="rgb(179 179 179)">
                    <CardHeader pad="medium">  <img src={img2} style={{ height: 100, width: 100 }} /> </CardHeader>
                    <CardBody pad="medium"><h1 style={middleButton}> EMBELEZAMENTO </h1>
                        <span style={middleButton}> Estamos aqui para deixar seu carro lindo sempre. Arranhões, amassados, troca de peças, polimento e tudo de estética automotiva
                        </span></CardBody>
                </Card>

                <Card height="medium" width="medium" background="rgb(179 179 179)">
                    <CardHeader pad="medium"> <img src={img3} style={{ height: 100, width: 100 }} /> </CardHeader>
                    <CardBody pad="medium">
                        <h1 style={middleButton}> FUNILARIA/MARTELINHO </h1>
                        <span style={middleButton}> Fazemos todo tipo de reparo em carroceria, desde um Martelinho até funilaria total de seu veículo
                        </span>
                    </CardBody>
                </Card>
            </Bx>
            <Box style={{ justifyContent: 'center', alignItems: 'center', paddingTop: '2%', paddingBottom: '2%', backgroundColor: 'white' }}>
                <h1 style={{ textAlign: 'center' }}> MARTELINGO MEGA BLASTER</h1>
                <p style={{ textAlign: 'center' }}> Nossa missão é promover uma experiência única, padrão de carro zero!  </p>
            </Box>

            <Box style={{
                backgroundImage: `url(${backgroundSemiFooter})`,
                WebkitBackgroundSize: 'cover',

            }}
            ><Stack
                direction="row"
                sx={{
                    width: '100%',
                    height: '100%',
                    justifyContent: 'center'
                }}
                spacing={5}>
                    <Stack maxWidth={400} sx={{ justifyContent: 'center', alignItems: 'center' }}>
                        <img src={car} style={{ height: 'auto', width: '100%' }} />
                    </Stack>

                    <Stack maxWidth={400}>
                        <h1> ESTAMOS TE ESPERANDO </h1>
                        <p style={{ color: '#dd3333' }}> Fala com gente, queremos saber como podemos deixar seu carro lindo, de novo!</p>

                        <p style={{ color: '#778a8e' }}>
                            Nós do Mega Blaster somos apaixonados por carros, e estamos aqui para oferecer o que há de mais moderno em estética automotiva.
                            Nossa equipe é treinada e qualificada para restaurar seu carro no padrão de carro zero
                        </p>

                        <Button variant="contained" sx={{ height: 'auto', width: '50%', backgroundColor: 'rgb(243 194 102)' }} color="error" >Fazer Orçamento {'>>'} </Button>

                    </Stack>
                </Stack>
            </Box>

            <Box style={{ justifyContent: 'center', alignItems: 'center', paddingTop: '2%', paddingBottom: '2%', backgroundColor: 'white' }}>
                <h1 style={{ textAlign: 'center' }}> ALGUNS DOS NOSSOS SERVIÇOS </h1>
                <p style={{ textAlign: 'center' }}> Desde que você tenha um carro nós temos todas a soluções para ele </p>

                <Stack direction="row" sx={{ width: '100%', height: '39vh', justifyContent: 'center' }}>

                    <Stack maxWidth={400} sx={{ justifyContent: 'center', alignItems: 'center' }}>
                        <img src={img1SemiFooter} style={{ height: 'auto', width: '80%' }} />
                        <h1 style={{ backgroundColor: '#3a3a3a', color: 'white', width: '80%', height: '15%', marginTop: '-10%', textAlign: 'center', fontSize: '1.3em' }}> PINTURA  </h1>
                    </Stack>
                    <Stack maxWidth={400} sx={{ justifyContent: 'center', alignItems: 'center' }}>
                        <img src={img2SemiFooter} style={{ height: 'auto', width: '80%' }} />
                        <h1 style={{ backgroundColor: '#3a3a3a', color: 'white', width: '80%', height: '15%', marginTop: '-10%', textAlign: 'center', fontSize: '1.2em' }}> POLIMENTO E VITRIFICÃÇÃO
                        </h1>

                    </Stack>
                    <Stack maxWidth={400} sx={{ justifyContent: 'center', alignItems: 'center' }}>
                        <img src={img3SemiFooter} style={{ height: 'auto', width: '80%' }} />
                        <h1 style={{ backgroundColor: '#3a3a3a', color: 'white', width: '80%', height: '15%', marginTop: '-10%', textAlign: 'center', fontSize: '1.2em' }}> FUNILARIA E MARTELINHO
                        </h1>

                    </Stack>

                    <Stack maxWidth={400} sx={{ justifyContent: 'center', alignItems: 'center' }}>
                        <img src={img4SemiFooter} style={{ height: 'auto', width: '80%' }} />
                        <h1 style={{ backgroundColor: '#3a3a3a', color: 'white', width: '80%', height: '15%', marginTop: '-10%', textAlign: 'center', fontSize: '1.2em' }}> PINTURA DE RODAS
                        </h1>
                    </Stack>
                </Stack>

                <Stack sx={{ justifyContent: 'center', alignItems: 'center' }}>
                    <Button variant="contained" sx={{ height: 'auto', width: '20%', alignItems: 'center', backgroundColor: '#030303' }} color="error" >Ver todos os Serviços {'>>'} </Button>
                </Stack>
            </Box>
            <Stack sx={{ backgroundColor: 'rgb(179 179 179)', width: '100%', height: '100%', justifyContent: 'center', alignItem: 'center' }} spacing={2}>
                <h1 style={footerButton1}>FACILIDADE E CONVENIÊNCIA</h1>
                <p style={footerButton2}> Buscamos seu carro e entregamos em domicílio.</p>

                <Bx direction='row' gap='40%' pad={{ left: 'xlarge' }}>
                    <h1 style={{ color: 'white' }}> Faça um Orçamento Gratuito: </h1>

                    <button style={{ height: '27%', width: '17%', alignItems: 'center', backgroundColor: '#1f2021', fontSize: 12, borderRadius: '4px', marginTop: '2%', color: 'white' }} >Ver todos os Serviços {'>>'} </button>
                </Bx>
            </Stack>

            <Stack direction="row" sx={{ backgroundColor: 'white', width: '100%', height: '25vh', justifyContent: 'center', alignItem: 'center', marginBottom: '5%' }} spacing={10}>

                <Stack maxWidth={500}>
                    <h1 style={{ color: 'rgb(243 194 102)' }}> Sobre nós </h1>
                    <p style={{ color: 'black' }}> O Martelinho Mega Blaster é MAIS MODERNO E COMPLETO centro de estética automotiva do Rio de Janeiro. Instalado em um terreno de mais de 5000 m2 no coração da Barra da Tijuca oferecendo tudo que seu carro precisa para ficar ainda mais lindo. Aqui oferecemos Serviços de alta qualidade e  atendimento personalizado.
                    </p>
                </Stack>
            </Stack>
        </>
    );
}

export default Home;
